.App {
    display: flex;
    justify-content: center;
    margin: 30px;
}
.container {
    width: 300px;
    height: 450px;
    padding: 30px;
    background-color:#dcedc8;
}
input {
    width: 220px;
    height: 40px;
    margin-right: 10px;
    margin-bottom: 30px;
    padding-left: 10px;
    background-color: white;
    border: none;
}
input:focus {outline: none;}
.add {
    width: 50px;
    height: 40px;
    background-color: white;
    border: none;
    font-size: large;
}
.todo-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 250px;
    height: 15px;
    padding: 20px;
    background-color: white;
    margin-bottom: 20px;
}
.todo-item .buttons {
    display: flex;
}
.todo-item .achieve, .todo-item .del {
    width: 30px;
    height: 30px;
    background-color: #dcedc8;
    border: none;
    border-radius: 50%;
    margin-left: 8px;
}
.achieve {
    background-color: #558b2f;
}
.del {
    font-size: large;
}